import { getParameter, namespace, toBoolean } from './utilities';

const epoch = Date.now();

const suffix = () => `(+${Date.now() - epoch}ms)`;

/**
 * Global loggers.
 */
const loggers = new Set();

const isDebugEnabled = toBoolean(
    getParameter('tacocat.debug', {}, { metadata: false }),
    typeof process !== 'undefined' && process.env?.DEBUG
);

/**
 * Returns `log` object which provides methods for unified error/debug logging
 * from a `source` module specified by its name.
 * Debugging logs are disabled by default and can be enabled by setting `process.env.DEBUG` to true
 * or the session storrage entry `tacocat.debug` to `true`.
 *
 * @type { import("./types").createLog }
 */
function createLog(source) {
    const prefix = `[${namespace}/${source}]`;
    const assert = (condition, message, ...args) => {
        if (!condition) {
            error(message, ...args);
            return false;
        }
        return true;
    };
    const debug = isDebugEnabled
        ? (message, ...args) => {
              console.debug(`${prefix} ${message}`, ...args, suffix());
          }
        : () => {};

    const error = (message, ...args) => {
        const prefixedMessage = `${prefix} ${message}`;
        loggers.forEach(([errorLogger]) =>
            errorLogger(prefixedMessage, ...args)
        );
    };

    const warn = (message, ...args) => {
        const prefixedMessage = `${prefix} ${message}`;
        loggers.forEach(([, warnLogger]) =>
            warnLogger(prefixedMessage, ...args)
        );
    };
    return { assert, debug, error, warn };
}

/**
 *
 * @type { import("./types").registerLogger }
 */
function registerLogger(errorLogger, warnLogger) {
    const logger = [errorLogger, warnLogger];
    loggers.add(logger);
    return () => {
        loggers.delete(logger);
    };
}

registerLogger(
    (message, ...args) => {
        console.error(message, ...args, suffix());
    },
    (message, ...args) => {
        console.warn(message, ...args, suffix());
    }
);

export { createLog, registerLogger };
