const namespace = 'tacocat.js';

const delay = (timeout = 100) =>
    new Promise((resolve) => setTimeout(resolve, timeout));

// NOTE: `toLowerCase` performs faster than `localeCompare`
const equalsCaseInsensitive = (value1, value2) =>
    String(value1 ?? '').toLowerCase() == String(value2 ?? '').toLowerCase();

const escapeHtml = (html) =>
    `${html ?? ''}`.replace(
        /[&<>'"]/g,
        (tag) =>
            ({
                '&': '&amp;',
                '<': '&lt;',
                '>': '&gt;',
                "'": '&#39;',
                '"': '&quot;',
            }[tag] ?? tag)
    ) ?? '';

/**
 * Retrieves parameter from location querystring, document metas,
 * session storage or local storage.
 * @param {string} key
 * Common key of parameter in some sources.
 * @param {Record<string, any>} defaults
 * @param {{
 *  search: boolean | string;
 *  metadata: boolean | string;
 *  storage: boolean | string;
 * }} options
 * Boolean flags allowing retrieval of parameter from particular type of source.
 * Or alternating string keys for various types of source.
 * @returns {string|undefined}
 */
function getParameter(
    key,
    defaults = {},
    { metadata = true, search = true, storage = true } = {}
) {
    let param;
    if (search && param == null) {
        const params = new URLSearchParams(window.location.search);
        const searchKey = isString(search) ? search : key;
        param = params.get(searchKey);
    }
    if (storage && param == null) {
        const storageKey = isString(storage) ? storage : key;
        param =
            window.sessionStorage.getItem(storageKey) ??
            window.localStorage.getItem(storageKey);
    }
    if (metadata && param == null) {
        const metadataKey = toKebabCase(isString(metadata) ? metadata : key);
        /** @type {HTMLMetaElement} */
        const element = document.documentElement.querySelector(
            `meta[name="${metadataKey}"]`
        );
        param = element?.content;
    }
    return param == null ? defaults[key] : param;
}

const ignore = () => {
    /* do nothing */
};

/**
 * @type { import('./types').isArray }
 */
const isArray = (value) => Array.isArray(value);

/**
 * @type { import('./types').isBoolean }
 */
const isBoolean = (value) => typeof value === 'boolean';

/**
 * @type { import('./types').isFunction }
 */
const isFunction = (value) => typeof value === 'function';

/**
 * @type { import('./types').isNumber }
 */
const isNumber = (value) => typeof value === 'number';

/**
 * @type { import('./types').isObject }
 */
const isObject = (value) => value != null && typeof value === 'object';

/**
 * @type { import('./types').isRegExp }
 */
const isRegExp = (value) => value != null && value instanceof RegExp;

/**
 * @type { import('./types').isString }
 */
const isString = (value) => typeof value === 'string';

/**
 * @type { import('./types').isNotEmptyString }
 */
const isNotEmptyString = (value) => isString(value) && value;

const isPositiveFiniteNumber = (value) =>
    isNumber(value) && Number.isFinite(value) && value > 0;

const isPositiveNumber = (value) => isNumber(value) && value > 0;

/**
 * Deletes all properties on `target` object having values which do not pass `test`.
 * If `test` is not provided, deletes all properties having `null` or `undefined` value.
 * @template Target
 * @param { Target } target
 * @param { (value) => boolean } test
 * @returns { Partial<Target> }
 */
function omitProperties(
    target,
    test = (value) => value == null || value === ''
) {
    if (target != null) {
        Object.entries(target).forEach(([key, value]) => {
            if (test(value)) delete target[key];
        });
    }
    return target;
}

const toArray = (value) => (isArray(value) ? value : [value]);

/**
 * @type { import('./types').toBoolean }
 */
function toBoolean(value, defaultValue) {
    if (isBoolean(value)) return value;
    const string = String(value);
    if (string === '1' || string === 'true') return true;
    if (string === '0' || string === 'false') return false;
    return defaultValue;
}

/**
 * Finds given `value` among values of the `enumeration` object
 * using case-insensitive string comparison.
 * Returns found value or `defaultValue` or first value of `enumeration`
 * depending of what was provided.
 * @template T
 * @param {any} value
 * @param {T} enumeration
 * @param {T[keyof T]} [defaultValue]
 * @returns {T[keyof T]}
 */
function toEnumeration(value, enumeration, defaultValue) {
    const values = Object.values(enumeration);
    return (
        values.find((candidate) => equalsCaseInsensitive(candidate, value)) ??
        defaultValue ??
        values[0]
    );
}

/**
 * Converts value to `kebab-case` string.
 * @param {any} value - value to convert
 */
function toKebabCase(value = '') {
    return String(value)
        .replace(
            /(\p{Lowercase_Letter})(\p{Uppercase_Letter})/gu,
            (_, p1, p2) => `${p1}-${p2}`
        )
        .replace(/\W+/gu, '-')
        .toLowerCase();
}

function toPositiveFiniteInteger(value, defaultValue = 1) {
    if (!isNumber(value)) {
        value = Number.parseInt(value, 10);
    }
    if (!Number.isNaN(value) && value > 0 && Number.isFinite(value)) {
        return value;
    }
    return defaultValue;
}

export {
    delay,
    equalsCaseInsensitive,
    escapeHtml,
    getParameter,
    ignore,
    isArray,
    isBoolean,
    isFunction,
    isNotEmptyString,
    isNumber,
    isObject,
    isPositiveFiniteNumber,
    isPositiveNumber,
    isRegExp,
    isString,
    namespace,
    omitProperties,
    toArray,
    toBoolean,
    toEnumeration,
    toKebabCase,
    toPositiveFiniteInteger,
};
