import { Term, Commitment } from '@pandora/data-models-odm';

// TODO: consider moving plan types to pandora
const ABM = 'ABM';
const PUF = 'PUF';
const M2M = 'M2M';
const PERPETUAL = 'PERPETUAL';
const P3Y = 'P3Y';

const TAX_INCLUSIVE_DETAILS = 'TAX_INCLUSIVE_DETAILS';
const TAX_EXCLUSIVE = 'TAX_EXCLUSIVE';

const PlanType = {
    ABM: ABM,
    PUF: PUF,
    M2M: M2M,
    PERPETUAL: PERPETUAL,
    P3Y: P3Y,
};

const planTypes = {
    [ABM]: { commitment: Commitment.YEAR, term: Term.MONTHLY },
    [PUF]: { commitment: Commitment.YEAR, term: Term.ANNUAL },
    [M2M]: { commitment: Commitment.MONTH, term: Term.MONTHLY },
    [PERPETUAL]: { commitment: Commitment.PERPETUAL, term: undefined },
    [P3Y]: { commitment: Commitment.THREE_MONTHS, term: Term.P3Y },
};

const errorValueNotOffer = 'Value is not an offer';

/**
 * Computes the plan type using commitment and term and applies it to the passed offer object.
 * @param {*} offer
 * @returns offer object with an additional planType key/value.
 */
const applyPlanType = (offer) => {
    if (typeof offer !== 'object') return errorValueNotOffer;
    const { commitment, term } = offer;
    const planType = getPlanType(commitment, term);
    return { ...offer, planType };
};

/**
 * Returns the business dimensions for a given plan type.
 * @param {*} planType
 * @returns an object with commitment and term keys for a valid plan type.
 */
const explodePlanType = (planType) => planTypes[planType] || {};

/**
 * Returns the plan type text form the given commitment and term
 * @param {*} commitment
 * @param {*} term
 * @returns plan type text
 */
const getPlanType = (commitment, term) => {
    switch (commitment) {
        case undefined:
            return errorValueNotOffer;
        case '':
            return '';
        case Commitment.YEAR:
            return term === Term.MONTHLY
                ? ABM
                : term === Term.ANNUAL
                    ? PUF
                    : '';
        case Commitment.MONTH:
            return term === Term.MONTHLY ? M2M : '';
        case Commitment.PERPETUAL:
            return PERPETUAL;
        case Commitment.TERM_LICENSE:
            return term === Term.P3Y ? P3Y : '';
        default:
            return '';
    }
};

/**
 * Updates the offer price and priceWithoutDiscount from tax exclusive prices
 * @param {*} offer
 * @returns
 */
function forceTaxExclusivePrice(offer) {
    const { priceDetails } = offer;
    const {
        price,
        priceWithoutDiscount,
        priceWithoutTax,
        priceWithoutDiscountAndTax,
        taxDisplay,
    } = priceDetails;

    if (taxDisplay !== TAX_INCLUSIVE_DETAILS) return offer;

    const amendedOffer = {
        ...offer,
        priceDetails: {
            ...priceDetails,
            price: priceWithoutTax ?? price,
            priceWithoutDiscount:
                priceWithoutDiscountAndTax ?? priceWithoutDiscount,
            taxDisplay: TAX_EXCLUSIVE,
        },
    };
    // trial offers have sometimes have to get their price looked at unit_without_discount
    // https://wiki.corp.adobe.com/x/a8ikeg
    if (
        amendedOffer.offerType === 'TRIAL' &&
        amendedOffer.priceDetails.price === 0
    ) {
        amendedOffer.priceDetails.price =
            amendedOffer.priceDetails.priceWithoutDiscount;
    }
    return amendedOffer;
}

export {
    ABM,
    PUF,
    M2M,
    PERPETUAL,
    PlanType,
    applyPlanType,
    explodePlanType,
    getPlanType,
    forceTaxExclusivePrice,
};
